/*
*   Default implementation for user fields
*/
bizagi.rendering.smartphone.basicUserField.extend("bizagi.rendering.smartphone.designUserField", {}, {

    /*
    *   Template method to implement in each device to customize each render after processed
    */
    getEditableControl: function () {
        var self = this;
        var $control = self.getControl();
        var properties = self.properties;
        var controlName = properties.control + ' Custom control';
        var tmpl = '<div class="bz-input-icon bz-widget--fake"><input class="bz-input-icon__input bz-space--inline-s" placeholder="' + controlName + '"/><svg class="bz-icn bz-space--inline-s" width="32px" height="32px"><use xlink:href="#gear" /></svg></div>';

        $control.addClass("bz-widget__control--fake");
        
        self.input = $(tmpl).appendTo($control);
    },

    /*
	*  Dont send the data to the server
	*/
    collectData: function (renderValues) {
    },

    getDisplayValue: function () {
        var self = this;
        var properties = self.properties;
        var controlName = properties.control;
        return "Userfield in design mode: " + controlName ;
    },

    getReadonlyControl: function () {
        var self = this;
        var $control = self.getControl();
        var properties = self.properties;
        var controlName = properties.control + ' Custom control';
        var tmpl = '<div class="bz-input-icon bz-widget--fake"><input class="bz-input-icon__input bz-space--inline-s" placeholder="' + controlName + '/><svg class="bz-icn bz-space--inline-s" width="32px" height="32px"><use xlink:href="#gear" /></svg></div>';

        $control.addClass("bz-widget__control--fake");
        
        var myControl = $(tmpl).appendTo($control);
        return myControl;
    }
});