/*
*   Name: BizAgi Form Modeler Collection Navigator Extension
*   Author: Alexander Mejia
*   Date: 02-12-213
*   Comments:
*   -   This script will redefine the collection navigator class to adjust to form modeler
*/
 

bizagi.rendering.smartphone.collectionnavigator.original = $.extend(true, {}, bizagi.rendering.smartphone.collectionnavigator.prototype);
$.extend(bizagi.rendering.smartphone.collectionnavigator.prototype,  {

    postRender: function () {
        var self = this;
     
        // Call original method
        bizagi.rendering.smartphone.collectionnavigator.original.postRender.apply(this, arguments);

        $.when(self.isRendered())
            .done(function () {
                var properties = self.properties;
                var control = self.getControl();
                var canvas = control.find(".bz-collectionnavigator-navigationform");

                if (properties.navigationform) {
                    setTimeout(function () {
                        self.triggerGlobalHandler("refreshControl", {
                            guid: properties.guid,
                            canvas: canvas
                        });
                    }, 0);
                }
            });
    }
});
