
/*
*   @tittle: Bizagi FormModeler Editor Hacks Rendering Form
*   @authors: UI Forms Team
*   @Comments:
*   -   This script will define basic stuff for enable the rendering Form
*		in the formModeler (delete div button container)
*/


bizagi.rendering.smartphone.form.original = $.extend( true, {}, bizagi.rendering.smartphone.form.prototype );
$.extend(bizagi.rendering.smartphone.form.prototype, {
    /*
     *   Renders the current container
     *   Removes buttons from original rendering
     */
    postRenderContainer: function (form) {
        var self = this;
        var $form = form;

        (self.children.length === 0) ? $form.attr("drophere", bizagi.localization.getResource("formmodeler-component-editor-layout-drophere-containers"))
                                     : $form.removeAttr("drophere"); 
                                                     
        // Call original rendering
        bizagi.rendering.smartphone.form.original.postRenderContainer.apply(this, arguments);
        
                               
        // Remove button container
        var allowButtons = typeof (self.params.allowButtons) !== undefined ? self.params.allowButtons : false;
        if (!allowButtons) {
            $(".ui-bizagi-button-container", form).remove();
        }

        if (self.properties.messageValidation && self.properties.messageValidation.length > 0) {
            var messageElement = self.createMessageValidationElement(self.properties.messageValidation);
            form.prepend(messageElement);
        }

        return form;
    },

    createMessageValidationElement: function (message) {
        var self = this;
        var resource = bizagi.localization.getResource("formmodeler-validations-form");

        var messageElement = $('<div class="ui-message-validation"/>');
        $('<span>' + resource + '<i class="biz-ico bz-studio bz-warning-red_16x16_standard" title="' + message + '"></i></span>').appendTo(messageElement);
        $(messageElement, "div").click(function () {
            self.triggerGlobalHandler("showPropertiesForm");
        });

        return messageElement;
    },


    /*
    *   Manages scrolling  event
    */   
    onScrollingForm:function($mainPanel, ev){
        var mainElement = $mainPanel[0];               
        var boundingClient = mainElement.getBoundingClientRect();
        var clientTopY = boundingClient.top;
        var clientButtonY = boundingClient.bottom
               
        function isInTop(){
            return Math.abs(ev.clientY - clientTopY) <= 20;
        }
        
        function isInBottom(){
            return Math.abs(clientButtonY - ev.clientY) <= 30;    
        }

        function move(){            
            var factor = isInTop() ? -1 : 1;                        
            var scrollTop = $mainPanel.scrollTop();
            
            if (factor === 1) {                                
                if (scrollTop > (mainElement.scrollHeight - mainElement.offsetHeight)){
                    return;
                }
            }
            
            var delta = scrollTop + (40 * factor)                
            $mainPanel.scrollTop(delta);                                    
        }      

        // Top border
        if (isInTop() || isInBottom()){                           
            setTimeout(move, 100);   
        }        
    }
    
    
    
});
   