/*
*   Name: BizAgi Form Modeler Tab Extension
*   Author: Diego Parra
*   Comments:
*   -   This script will redefine the tab class to adjust to form modeler
*/

bizagi.rendering.smartphone.tab.original = $.extend(true, {}, bizagi.rendering.smartphone.tab.prototype);
$.extend(bizagi.rendering.smartphone.tab.prototype, {
    /* 
    *   Template method to implement in each device to customize each container after processed
    */
    postRenderContainer: function ($tab) {
        var self = this;
        var properties = self.properties;

        // Call original method        
        bizagi.rendering.smartphone.tab.original.postRenderContainer.apply(this, arguments);

        // If the current form is readonly return;
        if (self.isReadOnlyForm()) {
            return;
        }

        var $addButton = $('<div class="bz-tab--add"><svg class="ico_16x16"><use xlink:href="#icon-plus"></use></svg></div>');
        var $offsetLeft = $('<div style="display: none;"><svg class="ico_16x16 offset__left-indicator"><use xlink:href="#icon-arrow-left"></svg></div>');
        var $offsetRigth = $('<div style="display: none;"><svg class="ico_16x16 offset__right-indicator"><use xlink:href="#icon-arrow-rigth"></svg></div>');
        var $tabContainer = $('.bz-tabs', $tab).first();

        $tabContainer
            .wrap('<div class="bz__tabs--container"></div>')
            .before($offsetLeft)
            .after($addButton)
            .after($offsetRigth)
            .on('updatePositions', function (ev, info) {
                setTimeout(function () {

                    var $container = $(ev.currentTarget);

                    self.updatePositions($offsetLeft, $offsetRigth);
                    self.updateErrorPositions($container);
                }, 100);
            });

        $offsetLeft.click(function (event) {
            event.stopPropagation();
            var htmlTabELement = $('.bz-tabs', self.container);
            htmlTabELement.scrollLeft(htmlTabELement.scrollLeft() - 15);
            self.updatePositions($offsetLeft, $offsetRigth);
        });

        $offsetRigth.click(function (event) {
            event.stopPropagation();
            var htmlTabELement = $('.bz-tabs', self.container);
            htmlTabELement.scrollLeft(htmlTabELement.scrollLeft() + 15);
            self.updatePositions($offsetLeft, $offsetRigth);
        });

        // Add handlers if the tab isn't contained in a nested form
        if (!self.isContainedInNestedForm() && !self.isContainedInCollectionNavigator()) {

            // Add handler
            $addButton.click(function (ev) {
                ev.stopImmediatePropagation();
                // Publish addtab event to the view
                self.triggerGlobalHandler("addtab", {guid: properties.guid});
                return false;
            });

            // Assign tab headers to children
            $.each(self.children, function (i, tabItem) {
                var $tabHeader = $("li[data-reference= #ui-bizagi-tab-" + tabItem.properties.guid + "]", $tab);
                tabItem.setTabHeader($tabHeader);
            });

            // Make headers sortable
            var header = $(".bz-tabs", $tab);
            header.sortable({
                items: "li[data-reference]",
                revert: true,
                distance: 10,
                axis: "x",
                cursorAt: {top: -3, left: 0},
                placeholder: "bz-tab-placeholder",
                delay: 150,
                start: function (e, ui) {
                    self.sortStart(e, ui);
                },
                tolerance: "pointer",
                stop: function (e, ui) {
                    self.sortFinish(e, ui);
                }
            });
        }

        $tab.data({
            guid: self.properties.guid,
            display: self.properties.displayName,
            type: self.properties.type
        });

        $tab.on("tabsload", function (event, ui) {
            self.triggerGlobalHandler("controlRefreshCanvas", {});
        }).on("tabsactivate", function (event, ui) {
            self.triggerGlobalHandler("controlRefreshCanvas", {});
        });

        setTimeout(function () {
            self.updatePositions($offsetLeft, $offsetRigth);
        }, 100);

        var item = (self.children || []).find(function (item) {
            if (item.properties && item.properties.messageValidation) return item;
        });

        if (item && (item.properties && item.properties.messageValidation)) {
            self.showValidationMessage(item.properties.messageValidation);
        }

    },

    updatePositions: function ($offsetLeft, $offsetRigth) {
        var $htmlTabELement = $('.bz-tabs', this.container).get(0);

        $offsetLeft.css({display: "none"});
        $offsetRigth.css({display: "none"});

        if ($htmlTabELement.scrollLeft > 0) {
            $offsetLeft.css({display: "flex"});
        }

        if ($htmlTabELement.scrollWidth > ($htmlTabELement.clientWidth + $htmlTabELement.scrollLeft + 30)) {
            $offsetRigth.css({display: "flex"});
        }
    },

    /*
    *   Manages the drag start event when sorting the header tabs
    */
    sortStart: function (event, ui) {
        var self = this;

        var index = $(ui.item).parent().children("li[data-reference]").not(".bz-tab-placeholder").index(ui.item);
        ui.item.data("start-position", index);
        self.initialPosition = index;
        return true;
    },

    /*
    *   Manages the drag end event when sorting the header tabs
    */
    sortFinish: function (event, ui) {
        var self = this;
        var index = $(ui.item).parent().children("li[data-reference]").not(".bz-tab-placeholder").index(ui.item);

        // Trigger sort finish event
        self.triggerGlobalHandler("sortfinish", {
            source: {},
            initialPosition: ui.item.data("start-position") || self.initialPosition,
            finalPosition: index,
            sourceContainer: self.properties.guid,
            targetContainer: self.properties.guid
        });

        return true;
    },

    updateErrorPositions: function ($container) {
        var $currentTabItem = $container.find("li.active");

        $(".bz-indicator-bar", $container).removeClass("bz-indicator-bar--error");

        if ($currentTabItem && $currentTabItem.hasClass("bz-tab--error")) {
            $(".bz-indicator-bar", $container).addClass("bz-indicator-bar--error");
        }
    }

});